import {createApp} from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

//Masonry grid
import MasonryWall from "@yeger/vue-masonry-wall";

// Vuetify
import '@mdi/font/css/materialdesignicons.css'
import 'vuetify/styles'
import {createVuetify} from 'vuetify/dist/vuetify'
import * as components from 'vuetify/components'
import * as directives from 'vuetify/directives'

const vuetify = createVuetify({
    components,
    directives,
    icons: {
        defaultSet: 'mdi',
    },
})


const app = createApp(App);

app.use(store).use(router).use(vuetify).use(MasonryWall).mount('#app')

// Use HTML5 history mode
if (window.history && window.history.pushState) {
    app.config.devtools = true;
    router.beforeEach((to, from, next) => {
        const fullPath = window.location.href
        if (fullPath.includes("#")) {
            next("/")
        } else {
            next()
        }
    })
    window.addEventListener('popstate', () => {
        app._routeChanged = true
        router.go(app._routeChanged ? 0 : -1)
        app._routeChanged = false
    }, false)
    router.replace(window.location.pathname.replace(process.env.BASE_URL, "/"))
}
